import { Component, HostBinding } from '@angular/core';
import { FullscreenOverlayContainer } from '@angular/cdk/overlay';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
    animations: [
        trigger('arrowAnimation', [
            state('default', style({ transform: 'rotate(-180deg)' })),
            state('rotated', style({ transform: 'rotate(0)' })),
            transition('rotated => default', animate('200ms ease-out')),
            transition('default => rotated', animate('200ms ease-in'))
        ])
    ]
})


export class AppComponent {
    title = 'app-contable';

    private userTheme: string = 'dark-theme'; // REMOVE
    public colores: string = 'blue';
    public isDarkmode: boolean;

    public notificationButtonColor: string = "none";
    public notificationBoxDisplay: boolean = false;
    public notificationBoxHideBadge: boolean = false;
    public notificationBoxCount: number = 4;

    public employeeButtonColor: string = "none";
    public employeeBoxDisplay: boolean = false;
    public employeeBoxHideBadge: boolean = false;
    public employeeBoxCount: number = 2;

    public configBoxDisplay: boolean = false;
    public configButtonColor: string = "none";

    public arrowState: string = 'rotated';
    public isActive: boolean = false;
    public slideResult: boolean = false;

    contentNav = [
        { name: "PAGOS", path: "", icon: "attach_money" },
        { name: "TAREAS", path: "", icon: "rule" },
        { name: "TICKETS", path: "", icon: "text_snippet" },
        { name: "CLIENTES", path: "", icon: "people_outline" },
        { name: "ESTADISTICAS", path: "", icon: "insert_chart_outlined" },
    ] // Remover al seleccionar tema y cambiar por secciones

    notifications = [
        { title: "Nuevo contribuyente", text: "Estudio jurídico Fernandez se ha agregado a la lista de clientes con el plan básico", route: "", time: "57 minutos" },
        { title: "Empleado ausente", text: "Carlos Perez no se ha conectado hace 6 días", route: "", time: "29 minutos" },
        { title: "Tarea vencida", text: "Contactar con el estudio Perón", route: "", time: "true" },
        { title: "Aumento del dolar", text: "El dolar ha aumentado 2.00 ARS", route: "", time: "Hace un día" },

    ] // Remover al generar modulo de usuarios

    employees = [
        { photo: "https://i.pinimg.com/564x/b7/dd/a4/b7dda4e08083af0d832e5ca0bf95076c.jpg", name: "Maria Gutierrez", itsOnline: true, lastTime: "" },
        { photo: "https://hopeandhealingcenter.org/wp-content/uploads/2020/03/people-detail-480x480.jpg", name: "Rodolfo Gutierrez", itsOnline: true, lastTime: "" },
        { photo: "https://futurefornature.org/wp-content/uploads/2016/11/Els-van-Lavieren-2-credits-Mira-Sjoerd-Schimmel-okt-2019-480x480.jpg", name: "Julieta Peña", itsOnline: false, lastTime: "Hace 3 horas" },
    ] // Remover al generar modulo de usuarios

    userCheck = [
        { photo: "https://pymstatic.com/44247/conversions/arturo-torres-medium.jpg", user: "Emanuel García", },
    ] // Remover al generar modulo de usuarios


    @HostBinding('class') componentCssClass: any;

    constructor(public overlayContainer: FullscreenOverlayContainer) {
        this.setTheme(this.userTheme);
    }

    public rotate() {
        this.arrowState = (this.arrowState == 'default') ? 'rotated' : 'default';
    }

    public alertbox(action: string) {
        switch (action) {
            case 'notification':
                if (this.notificationBoxDisplay) {
                    this.notificationBoxDisplay = false;
                    this.notificationButtonColor = "none";
                } else {
                    this.notificationBoxDisplay = true;
                    this.notificationBoxCount = 0;
                    this.notificationBoxHideBadge = true;
                    this.notificationButtonColor = "warn";

                    this.configBoxDisplay = false;
                    this.configButtonColor = "none";

                    this.employeeBoxDisplay = false;
                    this.employeeButtonColor = "none";

                }
                break;
            case 'employee':
                if (this.employeeBoxDisplay) {
                    this.employeeBoxDisplay = false;
                    this.employeeButtonColor = "none";
                } else {
                    this.employeeBoxDisplay = true;
                    this.employeeButtonColor = "warn";
                    this.employeeBoxCount = 2;
                    this.employeeBoxHideBadge = false;

                    this.configBoxDisplay = false;
                    this.configButtonColor = "none";

                    this.notificationBoxDisplay = false;
                    this.notificationButtonColor = "none";
                }
                break;
            case 'config':
                if (this.configBoxDisplay) {
                    this.configBoxDisplay = false;
                    this.configButtonColor = "none";
                } else {
                    this.configBoxDisplay = true;
                    this.configButtonColor = "warn";

                    this.employeeBoxDisplay = false;
                    this.employeeButtonColor = "none";

                    this.notificationBoxDisplay = false;
                    this.notificationButtonColor = "none";
                }
                break;
        }
        //OPTIMIZAR AL CONECTAR CON LA DB 
    }

    public changeTheme() {
        this.setTheme((this.slideResult) ? 'light-theme' : 'dark-theme');
    }

    private setTheme(theme: string) {
        this.overlayContainer.getContainerElement().classList.toggle(theme);
        this.componentCssClass = theme;
        this.isDarkmode = (theme == 'light-theme') ? false : true;
    }

    public setStyle(){
        return (this.isDarkmode == true) ? {'background' : 'rgb(78, 78, 78)', color : 'white'}: {background : 'white'};
    }
}
