import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { AngularFireFunctions } from '@angular/fire/functions';

@Component({
	selector: 'app-test-afip',
	templateUrl: './test-afip.component.html',
	styleUrls: ['./test-afip.component.scss']
})
export class TestAfipComponent implements OnInit {

	public nombre: string = 'Sin datos';
	public apellido: string = 'Sin datos';
	public documento: string = 'Sin datos';
	public actividad: string = 'Sin datos';
	public sexo: string = 'Sin datos';
	public persona: string = 'Sin datos';
	public provincia: string = 'Sin datos';
	public domicilio: string = 'Sin datos';
	public loading: boolean = false;

	public selectedCuit: string;
	
	public cuits: string[] = [
		'20002307554',
		'20002460123',
		'20188192514',
		'20221062583',
		'20200083394',
		'20220707513',
		'20221124643',
		'20221064233',
		'20201731594',
		'20201797064',
		'30202020204',
		'30558515305',
		'30558521135',
		'30558525025',
		'30558525645',
		'30558529535',
		'30558535365',
		'30558535985',
		'30558539565',
		'30558564675',
		'20203032723',
		'20168598204',
		'20188153853',
		'20002195624',
		'20002400783',
		'20187850143',
		'20187908303',
		'20187986843',
		'20188027963',
		'20187387443'
	]

	constructor(private fns: AngularFireFunctions, private ref: ChangeDetectorRef) { }

	ngOnInit(): void {
		
	}

	public callAfip(): void {
		console.log(this.selectedCuit);
		const cuitToSend = this.selectedCuit;
		this.loading = true;
		const func = this.fns.httpsCallable('AfipCIPA4OnCall');
		const afip = func({cuit: Number.parseInt(cuitToSend)});
		afip.subscribe(data => this.responseAfip(JSON.parse(data)));
	}

	private responseAfip(data): void {
		console.log(data);

		if(data.nombre)
			this.nombre = data.nombre;
		else
			this.nombre = 'Sin datos';

		if(data.apellido)
			this.apellido = data.apellido;
		else
			this.apellido = 'Sin datos';

		if(data.numeroDocumento)
			this.documento = data.numeroDocumento;
		else
			this.documento = 'Sin datos';

		if(data.actividad && data.actividad[0] && data.actividad[0].descripcionActividad)
			this.actividad = data.actividad[0].descripcionActividad;
		else
			this.actividad = 'Sin datos';

		if(data.sexo)
			this.sexo = data.sexo;
		else
			this.sexo = 'Sin datos';

		if(data.tipoPersona)
			this.persona = data.tipoPersona;
		else
			this.persona = 'Sin datos';

		if(data.domicilio && data.domicilio[0] && data.domicilio[0].descripcionProvincia)
			this.provincia = data.domicilio[0].descripcionProvincia;
		else
			this.provincia = 'Sin datos';
		
		if(data.domicilio && data.domicilio[0] && data.domicilio[0].direccion)
			this.domicilio = data.domicilio[0].direccion;
		else
			this.domicilio = 'Sin datos';

		this.loading = false;
		this.ref.detectChanges();
	}


}
